@charset "UTF-8";
/* Slider.css */
.gallery {
  display: grid;
  grid-template-columns: repeat(4, 234px);
  -moz-column-gap: -100px;
       column-gap: -100px;
  margin: 30px;
  justify-items: center;
  align-items: stretch;
  justify-content: center;
}
@media (max-width: 769px) {
  .gallery {
    grid-template-columns: repeat(3, 1fr);
    -moz-column-gap: 25px;
         column-gap: 25px;
  }
}
@media (max-width: 426px) {
  .gallery {
    grid-template-columns: repeat(2, 1fr);
    -moz-column-gap: 20px;
         column-gap: 20px;
  }
}
@media (max-width: 376px) {
  .gallery {
    grid-template-columns: repeat(2, 1fr);
    -moz-column-gap: 15px;
         column-gap: 15px;
  }
}
@media (max-width: 321px) {
  .gallery {
    grid-template-columns: repeat(2, 1fr);
    -moz-column-gap: 15px;
         column-gap: 15px;
  }
}

.gallery-image {
  width: 100%;
  max-width: 200px; /* Максимальная ширина для изображений */
  height: auto;
  margin: 10px;
  cursor: pointer;
  transition: transform 0.2s, filter 0.2s, box-shadow 0.2s; /* Плавный эффект при наведении */
}

.gallery-image:hover {
  transform: scale(1.15); /* Увеличение изображения при наведении */
  filter: brightness(1.3); /* Увеличение яркости при наведении */
  box-shadow: 0 4px 15px rgb(139, 0, 0); /* Добавление тени */
}

.модалка {
  position: absolute;
  top: 100px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.modal {
  flex-direction: column;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  overflow-y: auto;
  z-index: 1000;
}

.modal-content {
  max-width: 90%;
  max-height: 90%;
}
@media (max-width: 2000px) {
  .modal-content {
    max-width: 65%;
  }
}
@media (max-width: 1441px) {
  .modal-content {
    max-width: 65%;
  }
}
@media (max-width: 1025px) {
  .modal-content {
    max-width: 50%;
  }
}
@media (max-width: 769px) {
  .modal-content {
    max-width: 55%;
  }
}
@media (max-width: 426px) {
  .modal-content {
    max-width: 90%;
  }
}
@media (max-width: 376px) {
  .modal-content {
    max-width: 90%;
  }
}
@media (max-width: 321px) {
  .modal-content {
    max-width: 90%;
  }
}

