.close, .prev, .next {
  position: absolute;
  top: 50%;
  z-index: 10000;
  transform: translateY(-50%);
  color: white;
  font-size: 30px;
  cursor: pointer;
}
@media (max-width: 426px) {
  .close, .prev, .next {
    top: 88%;
  }
}

.close {
  font-size: 80px;
  top: 36px;
  right: 20px;
}

.prev {
  left: 0px;
}

.next {
  right: 0px;
