/* Slider.css */
.gallery {
    display: grid;
    grid-template-columns: repeat(4, 234px);
    column-gap: -100px;
    margin: 30px;
    justify-items: center;
    align-items: stretch;
    justify-content: center;
    @media (max-width: 769px) {
        grid-template-columns: repeat(3, 1fr);
        column-gap: 25px;
        
    }
    @media (max-width: 426px) {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 20px;
        
    }
    @media (max-width: 376px) {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 15px;
        
    }
    @media (max-width: 321px) {
        grid-template-columns: repeat(2, 1fr);
        column-gap: 15px;
        
    }
}

.gallery-image {
    width: 100%;
    max-width: 200px; /* Максимальная ширина для изображений */
    height: auto;
    margin: 10px;
    cursor: pointer;
    transition: transform 0.2s, filter 0.2s, box-shadow 0.2s; /* Плавный эффект при наведении */
}

.gallery-image:hover {
    transform: scale(1.15); /* Увеличение изображения при наведении */
    filter: brightness(1.3); /* Увеличение яркости при наведении */
    box-shadow: 0 4px 15px rgba(139, 0, 0, 1); /* Добавление тени */
}

.модалка{
    position: absolute;
    top: 100px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.modal {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.8);
    overflow-y: auto;
    z-index: 1000;
}

.modal-content {   
    max-width: 90%;
    max-height: 90%;
    
    @media (max-width: 2000px) {
        max-width: 65%;
        
    }
    @media (max-width: 1441px) {
        max-width: 65%;
        
    }
    @media (max-width: 1025px) {
        max-width: 50%;
    }
    @media (max-width: 769px) {
        max-width: 55%;
    }
    @media (max-width: 426px) {
        max-width: 90%;
    }
    @media (max-width: 376px) {
        max-width: 90%;
    }
    @media (max-width: 321px) {
        max-width: 90%;
        
        
    }
}

p.qwe{
    text-align: start;
    margin: 50px 21px;
    font-size: 28px;
    @media (max-width: 426px) {
        text-align: start;
        margin: 285px 21px;
        font-size: 28px;
    }
    @media (max-width: 376px) {
        text-align: start;
        margin: 313px 21px;
        font-size: 28px;
    }
    @media (max-width: 321px) {
        text-align: start;
        margin: 346px 21px;
        font-size: 28px;
    }
}

.close, .prev, .next {
    position: absolute;
    top: 50%;
    z-index: 10000;
    transform: translateY(-50%);
    color: white;
    font-size: 30px;
    cursor: pointer;
    @media (max-width: 426px) {
        top: 88%;
    }
    
    
}

.close {
    font-size: 80px;
    top: 36px;
    right: 20px;
}

.prev {
    left: 0px;
}

.next {
    right: 0px;
}